import React from 'react';
import {history} from './redux/createStore';
import './index.css';
import createStore from './redux';
import {Provider} from 'react-redux'
import {PersistGate} from 'redux-persist/integration/react'
import {useTranslation} from 'react-i18next'
import numeral from 'numeral'
import {setupNumeral} from './utils/format'
import Routes from "./Routes";

const {store, persistor} = createStore();

numeral.locale('en');
function App() {
    const {i18n} = useTranslation();
    return (
        <Provider store={store}>
            <PersistGate loading={null}
                         persistor={persistor}
                         onBeforeLift={
                             () => {
                                 i18n.changeLanguage(store.getState().lang);
                                 setupNumeral(store.getState().formats.monetary);

                                 // check history
                                 const defView = store.getState().settings.defaultView;

                                 // In case of multi converter
                                 if (defView == 1) {
                                     history.replace('/multi');
                                 } else {
                                     history.replace('/');
                                 }
                             }
                         }>
                <Routes/>
            </PersistGate>
        </Provider>
    );
}

export default App;
