import {createAction, handleAction} from 'redux-actions';
import Actions from '../constants/actions';

const updateRateUsTime = createAction(Actions.UPDATE_RATEUS_TIME);

const reducer = handleAction(
  Actions.UPDATE_RATEUS_TIME,
  (state, {payload}) => (new Date()).getTime(),
  0
)

const selector = (state) => (state.rateustime);

export {
  updateRateUsTime,
  selector,
  reducer
}
