import i18n from "i18next";
import {initReactI18next} from "react-i18next";


const langArray = ['ad', 'am', 'ba', 'bg', 'bn', 'cs', 'da',
    'de', 'el', 'en-GB', 'en-US', 'es', 'et', 'fi', 'fil', 'fr', 'ge', 'gu', 'he', 'hi', 'hr', 'hu',
    'id1', 'id2', 'it', 'ja', 'lt', 'lv', 'mr', 'nl', 'no', 'np', 'pl', 'pt-BR', 'ro', 'ru', 'sk', 'sl',
    'tj', 'sr', 'sv', 'ta', 'te', 'tj', 'tr', 'uk', 'uz', 'vi', 'ws', 'zh-CN'];
const resources = {};

export function getLanguageCodeFromBrowser(){
    const navLang = navigator.language;
    const langs = navigator.language.split('-');
    const langCode = langs[0];

    // return the equal ones
    for(let i = 0; i < langArray.length; i++){
        if (langArray[i] === navLang){
            return navLang;
        }
    }

    // if not found, then
    for (let i = 0; i < langArray.length; i++){
        if (langArray[i].startsWith(langCode)){
            return navLang;
        }
    }
    return 'en-US';
}


langArray.forEach(lang => {
    resources[lang] = {
        translation: require(`./assets/translations/${lang}`)
    }
});

i18n
    .use(initReactI18next) // passes i18n down to react-i18next
    .init({
        resources,
        lng: getLanguageCodeFromBrowser(),
        fallbackLng: "en-US",
        interpolation: {
            escapeValue: false
        }
    });

export default i18n;
