import React from 'react';
import styled from 'styled-components';
import {currency2Symbols, currencyISOs} from '../../assets/codes'
import {connect} from "react-redux";
import {useTranslation} from "react-i18next";
import {isEmpty} from 'lodash';
import {Col, Row} from "../../components";
import Space from "../../components/Space";
import Currency from "../../components/Currency";
import {image} from "../../utils";

const Component = ({keyword, style, className, onSelectItem, selectedItems, addedItems}) => {
    const {t} = useTranslation();

    const allList = React.useMemo(
        () => {
            return currencyISOs
              .filter(f => !addedItems.includes(f))
              .map(iso => {
                return {
                    iso,
                    description: t(iso)
                }
            });
        }, [t, addedItems]
    );

    const filteredList = React.useMemo(
        () => allList.filter(item => {
            if (isEmpty(keyword)) return true;
            const l = keyword.toLowerCase();
            return item.iso.toLowerCase().includes(l) || item.description.toLowerCase().includes(l);
        }), [allList, keyword]);

    return (
        <Container style={style} className={className}>
            {filteredList && filteredList.map(item => {
                return (
                    <ItemContainer onClick={() => onSelectItem && onSelectItem(item.iso)} isSelected={selectedItems.includes(item.iso)} key={item.iso}>
                        <Currency iso={item.iso} width={30}/>
                        <Space width={10}/>
                        <Text>{item.description} - {item.iso}({currency2Symbols[item.iso] || '$'})</Text>
                        <img src={image('add.png')} alt='addImage'/>
                    </ItemContainer>
                )
            })}
        </Container>
    )
};
const Container = styled(Col)`
    width: 100%;
    overflow-y: scroll;
    max-height: 220px;
    background-color:white;
`;
const ItemContainer = styled(Row)`
    padding: 10px;
    border-bottom:1px solid rgb(200, 200, 200);
    align-items:center;
    justify-content: flex-start;
    background-color:${props => props.isSelected ? 'lightgrey' : 'white'};
    &:hover {
       background-color:lightgrey;
    }
`;
const Text = styled.span`
    color: rgb(116, 116, 116);
    font-size: 16px;
    flex: 1;
`;
export default connect(
    state => ({
        addedItems: state.multi,
        lang: state.lang
    })
)(Component);
