import {createAction, handleActions} from 'redux-actions';
import Actions from '../constants/actions';
import produce from 'immer';

const setGradient = createAction(Actions.SET_GRADIENT);
const setColor = createAction(Actions.SET_COLOR);
const setDefaultView = createAction(Actions.SET_DEFAULT_VIEW);

const reducer = handleActions({
    [Actions.SET_GRADIENT] : (state, {payload}) => {
        return produce(state, s => {
            s.gradient = payload;
        });
    } ,
    [Actions.SET_COLOR] : (state, {payload}) => {
        return produce(state, s => {
            s.color = payload;
        });
    },
    [Actions.SET_DEFAULT_VIEW] : (state, {payload}) => {
        return produce(state, s => {
            s.defaultView = payload;
        });
    }
}, {
    gradient: false,
    color: '#A94442',
    defaultView: 0
});

export {
    reducer,
    setGradient,
    setColor,
    setDefaultView
}
