export function convert(rates, from, to, amount) {
    if (from === to) {
        return amount;
    }

    if (/*!rates.source ||*/ !rates.quotes) {
        return 0
        //throw new Error('No rates data');
    }

    const source = 'USD';

    const fromRate = (from === source) ? 1 : rates.quotes[from];

    if (!fromRate) {
        return 0
        //throw new Error('No rates data');
    }

    const toRate = (to === source) ? 1 : rates.quotes[to];

    if (!toRate) {
        return 0
        //throw new Error('No rates data');
    }
    return amount * fromRate / toRate;
}
