import {createAction, handleAction} from 'redux-actions';
import Actions from '../constants/actions'
import {getLanguageCodeFromBrowser} from "../i18n";

const setLang = createAction(Actions.SET_LANG);

const reducer = handleAction(
    Actions.SET_LANG,
    (state, {payload: lang}) => lang,
    getLanguageCodeFromBrowser()
);

const selector = (state) => (state.lang);

export {
    setLang,
    selector,
    reducer
}
