import React from 'react';
import {DragDropContext, Draggable, Droppable} from 'react-beautiful-dnd'
import styled from 'styled-components';
import {connect} from "react-redux";
import Item from "./Item";
import {removeCurrency, reorderCurrency, selector} from '../../redux/multi';
import {image} from "../../utils";
import {Col, Screen} from '../../components'
import {Link} from "react-router-dom";

const Multi = ({currencies, add, remove, reorder}) => {

    const onDragEnd = React.useCallback(({source, destination}) => {
        if (!destination) { return; }
        reorder(source.index, destination.index);
    }, [reorder]);
    return (
        <Screen>
            <DragDropContext onDragEnd={onDragEnd}>
                <Droppable droppableId="droppable">
                    {(provided) => (
                        <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            style={{maxHeight: 315, overflow: 'auto'}}
                        >
                                {
                                    currencies.map((item, index) => (
                                        <Draggable key={item} draggableId={item} index={index} shouldRespectForcePress={true}>
                                            {(provided) => (
                                                <div
                                                    ref = {provided.innerRef}
                                                    {...provided.draggableProps}
                                                    {...provided.dragHandleProps}
                                                >
                                                    <Item iso={item} onRemove={() => remove(item)}/>
                                                </div>
                                            )}
                                        </Draggable>
                                    ))
                                }
                                {provided.placeholder}
                        </div>
                    )}
                </Droppable>
            </DragDropContext>
            <AddButton>
                <Link to={'/multi/add'}>
                    <img src={image('plus.png')} alt={'add'}/>
                </Link>
            </AddButton>
        </Screen>
    )
};

const AddButton = styled(Col)`
    align-items: center;
    justify-content: center;
`;

export default connect(
    state => ({
        currencies: selector(state)
    }),
    dispatch => ({
        remove: (iso) => dispatch(removeCurrency(iso)),
        reorder: (oldIndex, newIndex) => dispatch(reorderCurrency(oldIndex, newIndex))
    })
)(Multi);
