import React from 'react';
import styled, {keyframes} from 'styled-components'
import './index.css';
import {Col, ImageButton, Row, Screen} from '../../components/index'
import CurrencySelector from './CurrencySelector';
import Space from '../../components/Space';
import CountryList from './CurrencyList';
import produce from 'immer';
import {connect} from 'react-redux';
import {convert} from '../../utils/rates';
import {setFavsFrom, setFavsTo, setFavsValue, swapFavs} from "../../redux/favs";
import numeral from 'numeral';
import {useTranslation} from "react-i18next";
import {webStoreRateLink} from '../../constants'
import {updateRateUsTime} from '../../redux/rateustime'
import {formatNumber} from '../../utils/format'
import Currency from '../../components/Currency'
import {image} from "../../utils";

const selectorState = {
    showFrom: false,
    showTo: false
};

selectorState.isCurrencyListVisible = function(){
    return this.showFrom || this.showTo;
};

const reducer = (state, {type, payload}) => {
    switch (type) {
        case 'SHOW_FROM':
            return produce(state, s => {
                s.showFrom = true;
                s.showTo = false;
            });
        case 'HIDE_FROM':
            return produce(state, s => {
                s.showFrom = false;
            });
        case 'SHOW_TO':
            return produce(state, s => {
                s.showFrom = false;
                s.showTo = true;
            });
        case 'HIDE_TO':
            return produce(state, s => {
                s.showTo = false;
            });
        default:
    }
};

function getShareLink(from, to, value, precision) {
    const prefix = `https://www.currency.wiki/${from.toLowerCase()}_${to.toLowerCase()}`;
    if (value === 1){
        return prefix;
    }
    return prefix + `?value=${value.toFixed(precision.length)}`;
}

const aWeekMilliseconds = 604800000;

const Main = (props) => {
    const [state, dispatch] = React.useReducer(reducer, selectorState);
    const [keyword, setKeyword] = React.useState('');
    const [showRateUS, setShowRateUs] = React.useState(false);
    const listDisplay = state.isCurrencyListVisible() ? 'flex' : 'none';
    const [showRateUsExpired] = React.useState(((new Date()).getTime() - props.rateustime) > aWeekMilliseconds);
    const [isEditing, setEditing] = React.useState(false);
    const [editValue, setEditValue] = React.useState('');

    const swap = () => {
        props.swapFavs();
        try {
            const newValue = convert(props.rates, props.from, props.to, parseFloat(props.value));
            props.setFavsValue(newValue);
        }catch(error){
            props.setFavsValue(1);
        }
    };

    const onSelectItem = (iso) => {
        if (state.showFrom) {
            props.setFavsFrom(iso);
        } else {
            props.setFavsTo(iso);
        }
    };

    const saveEnteredValue = (v) => {
        if (!v.length) {
            setEditValue('');
            return;
        }
        const number = parseFloat(v)
        if (isNaN(number)){
            return;
        }
        setEditValue(v);
        props.setFavsValueSelfEditing(number);
    };

    const { t } = useTranslation();
    const preStarTitle = 'Please Rate Us ,)';
    const starTitle = React.useMemo(
      () => {
          return t(preStarTitle);
      }, [t]
    );

    const _startEditing = () => {
        setEditValue(numeral(formatNumber(props.value, props.formats.precision)).value())
        setEditing(true);
    };

    const _stopEditing = () => {
        setEditing(false);
    };

    const _showRateUs = () => {
        setShowRateUs(true);
        props.updateRateUsTime();
    };

    const _renderCurrency = () => {
        if (isEditing) {
            return (
              <CurrencyInput
                type='number'
                autoFocus
                onChange={event => saveEnteredValue(event.target.value)}
                value={editValue}
                onFocus={_showRateUs}
                onBlur={_stopEditing}
                style={{color:'black'}}
                onKeyDown={(evt) => {
                    if (evt.keyCode === 27 || evt.keyCode === 13) {
                        _stopEditing()
                    }
                }}
              />)
        } else {
            return(
              <CurrencyInput as="span" color={props.color}>
                  {props.isSelfEditingValue ? props.value : formatNumber(props.value, props.formats.precision)}
              </CurrencyInput>
            )
        }
    };

    return (
      <Screen>
          <ListView>
              <Currency iso={props.from} style={{marginLeft: 5}}/>
              <CurrencyValue
                onClick={() => {
                    if (!isEditing) {
                        _startEditing();
                    }
              }}>{_renderCurrency()}</CurrencyValue>
              <ConvertArrow>
                  <div onClick={swap} ><ImageButton src={image('swap-icon.png')} alt="swap"/></div>
              </ConvertArrow>
          </ListView>
          <ChooseView>
              <CurrencySelector
                style={{flex: 1}}
                iso={props.from}
                onStartSearch={() =>{
                    dispatch({type: 'SHOW_FROM'});
                    setKeyword('')
                }}
                onStopSearch={() => dispatch({type: 'HIDE_FROM'})}
                onChangeSearch={setKeyword}
                displayDownArrow={!state.isCurrencyListVisible() || state.showFrom}
                color={props.color}
              />
              <Space width={25}/>
              <CurrencySelector
                style={{flex: 1}}
                iso={props.to}
                onStartSearch={() => {
                    dispatch({type: 'SHOW_TO'});
                    setKeyword('')
                }}
                onStopSearch={() => dispatch({type: 'HIDE_TO'})}
                onChangeSearch={setKeyword}
                displayDownArrow={!state.isCurrencyListVisible() || state.showTo}
                color={props.color}
              />
          </ChooseView>
          <BottomView>
              <CountryList
                style={{marginTop: -1, display:listDisplay}}
                keyword={keyword}
                onSelectItem = {onSelectItem}
              />
              {props.value > 0 && (
                <ResultView>
                    <div>
                        {formatNumber(convert(props.rates, props.from, props.to, props.value), props.formats.precision)} {props.to}
                    </div>
                    <LinkView>
                        <a href={getShareLink(props.from, props.to, props.value, props.formats.precision)} target='_blank'>
                            <img src={image('star2.png')} alt='link'/>
                        </a>
                    </LinkView>
                </ResultView>
              )}
              <RateViewArea>
                  {/*Only show once a week*/}
                  {
                      showRateUS && showRateUsExpired && (
                        <RateView>
                            <RateUs href={webStoreRateLink} target={'_blank'}
                                    onClick={props.updateRateUsTime}>
                                {starTitle} <StarSpan><StarImg src={image('star1.png')}/></StarSpan>
                            </RateUs>
                        </RateView>)
                  }
              </RateViewArea>
          </BottomView>
      </Screen>
    )
};

const ResultView = styled(Row)`
    justify-content:center;
    align-items: flex-end;
    font-size: 30px;
    color: white;
`;
const StarSpan = styled.span`
    position: relative;
    top: 4px;
`;
const StarImg = styled.img`
    width: 20px;
    height: 20px;
`;
const LinkView = styled.div`
    margin-left: 5px;
    margin-bottom: 15px;
`;
const ChooseView = styled(Row)`
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
`;
const ListView = styled(Row)`
    padding: 5px;
    background: white;
    align-items: center;
`;

const ConvertArrow = styled(Col)`
    align-items: center;
    justify-content: center;
    padding: 5px;
`;

const CurrencyValue = styled(Col)`
    flex: 1;
    align-items:center;
    justify-content:center;
`;

const CurrencyInput = styled.input`
    text-align: center;
    border: none;
    font-size: 25px;
    color: ${props => props.color};
    font-weight: bold;
    vertical-align: center;
    align-self: stretch;
`;

const BottomView = styled(Col)`
    flex: 1;
    position:relative;
    justify-content:flex-end;
`;

// Rate US fade animation
const fadeAnim = keyframes`
    0% { opacity: 0; }
    100% { opacity: 1; }
`

const RateViewArea = styled.div`
    height: 50px;
    margin-bottom: 80px;
`;

const RateView = styled.div`
    text-align: center;
    margin-top: 20px;
    font-size: 20px;
    color: white;
    animation: ${fadeAnim} ease-in 1s;
    margin-bottom: 30px;
`;

const RateUs = styled.a`
    color:white;
    text-decoration: none;
    &:hover{
       color: white;
    }
`;

export default connect(
  state => ({
      rates: state.rates,
      formats: state.formats,
      from: state.favs.from,
      to: state.favs.to,
      value: state.favs.value,
      rateustime: state.rateustime,
      isSelfEditingValue: state.isSelfEditing,
      color: state.settings.color,
  }),
  dispatch => ({
      setFavsFrom: (from) => dispatch(setFavsFrom(from)),
      setFavsTo: (to) => dispatch(setFavsTo(to)),
      setFavsValue: (value) => dispatch(setFavsValue(value, false)),
      setFavsValueSelfEditing: (value) => dispatch(setFavsValue(value, true)),
      swapFavs: () => dispatch(swapFavs()),
      updateRateUsTime: () => dispatch(updateRateUsTime())
  })
)(Main);
