import {createAction, handleActions} from 'redux-actions';
import Actions from '../constants/actions';
import produce from 'immer';

const setFavsFrom = createAction(Actions.SET_FAVS_FROM);
const setFavsTo = createAction(Actions.SET_FAVS_TO);
const setFavsValue = createAction(Actions.SET_FAVS_VALUE, (value, isSelfEditing) => ({value, isSelfEditing}));
const swapFavs = createAction(Actions.SWAP_FAVS);

const reducer = handleActions(
    {
        [Actions.SET_FAVS_FROM]: (state, {payload}) => {
            return produce(state, s => {
                s.from = payload
            })
        },
        [Actions.SET_FAVS_TO]: (state, {payload}) => {
            return produce(state, s => {
                s.to = payload
            })
        },
        [Actions.SET_FAVS_VALUE]: (state, {payload: {value, isSelfEditing}}) => {
            return produce(state, s => {
                s.value = value;
                s.isSelfEditing = isSelfEditing;
            })
        },
        [Actions.SWAP_FAVS]: (state) => {
            return produce(state, s => {
                const temp = s.from;
                s.from = s.to;
                s.to = temp;
            })
        }
    },
    {from:'USD', to:'USD', value: 1, isSelfEditing: false}
);

const selector = (state) => (state.favs);

export {
    setFavsFrom,
    setFavsTo,
    setFavsValue,
    swapFavs,
    selector,
    reducer
}
