import {applyMiddleware, compose, createStore} from 'redux';
import createSagaMiddleware from 'redux-saga';
import {createMemoryHistory} from 'history';
import {routerMiddleware} from 'connected-react-router';
import {persistReducer, persistStore} from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import {getLanguageCodeFromBrowser} from "../i18n";

const persistConfig = {
    key: 'root',
    storage,
    blacklist: ['router'],
};

export const history = createMemoryHistory();
export default (rootReducerCreator, rootSaga) => {
    const middleware = [];
    const enhancers = [];

    // Push router middleware
    middleware.push(routerMiddleware(history));


    // Saga Middleware
    const sagaMiddleWare = createSagaMiddleware();

    // Push saga middleware
    middleware.push(sagaMiddleWare);

    // Assemble Middleware
    enhancers.push(applyMiddleware(...middleware));

    const rootReducer = rootReducerCreator(history);

    const persistedReducer = persistReducer(persistConfig, rootReducer);


    // Create Store
    const store = createStore(
      persistedReducer,
      {
          lang: getLanguageCodeFromBrowser(),
          favs: {
              from: 'USD',
              to: 'EUR',
              value: 1
          }
      },
        compose(...enhancers)
    );

    const persistor = persistStore(store);

    // Kickoff root saga
    const sagasManager = sagaMiddleWare.run(rootSaga);

    return {
        store,
        persistor,
        sagasManager,
        sagaMiddleWare
    }
}
