import React from 'react';
import styled from 'styled-components';
import {Row} from "../../components";

const presetColors = [
    "#4A90E2",
	"#A94442",
    "#E0900C",
    "#8B572A",
    "#70B91F",
    "#75A144",
    "#BD10E0",
    "#9013FE",
    "#000000",
    "#4A4A4A",
    "#9B9B9B",
];

const ColorPicker = ({className, onPickColor}) => {
    return (
        <Container className={className}>
            {
                presetColors.map((color) => {
                    return (<ColorItem color={color} key={color} onClick={() => onPickColor(color)}/>)
                })
            }
        </Container>
    );
};


const Container = styled(Row)`
    flex-wrap: wrap;
`;

const ColorItem = styled.div`
    background-color: ${props => props.color};
    border-radius: 2px;
    border-color: #000000;
    margin-right: 10px;
    margin-bottom: 10px; 
    width: 25px;
    height: 25px;
    cursor: pointer;
`;

export default ColorPicker;
