import {combineReducers} from 'redux'
import configureStore, { history } from './createStore'
import rootSaga from '../saga/'
import { connectRouter } from 'connected-react-router'

export const reducers = (history) =>  combineReducers({
    router: connectRouter(history),
    lang: require('./lang').reducer,
    multi: require('./multi').reducer,
    rates: require('./rates').reducer,
    formats: require('./formats').reducer,
    favs: require('./favs').reducer,
    rateustime: require('./rateustime').reducer,
    ad: require('./ad').reducer,
    settings: require('./settings').reducer,
});

export default () => {
    let { store, sagasManager, sagaMiddleware, persistor } = configureStore(reducers, rootSaga);

    if (module.hot) {
        module.hot.accept(() => {
            const nextRootReducer = require('./').reducers(require('./createStore').history);
            store.replaceReducer(nextRootReducer);

            const newYieldedSagas = require('../saga/').default;
            sagasManager.cancel();
            sagasManager.done.then(() => {
                sagasManager = sagaMiddleware.run(newYieldedSagas);
            })
        })
    }
    return { store, persistor };
}
