import React from 'react';
import styled from "styled-components";
import {connect} from "react-redux";
import {setColor, setDefaultView, setGradient} from "../redux/settings";

const Currency = ({iso, ...props}) => {
    return (
        <Container {...props}>
            <Text>{iso.toUpperCase()}</Text>
        </Container>
    )
};

const Container = styled.div`
  width: 50px;
  height: 32px;
  display: flex;
  background-color:  ${props => props.color};
  align-items: center;
  justify-content: center;
`;

const Text = styled.div`
  font-size: 16px;
  font-weight: bold;
  color: white;
`;

export default connect(
    state => ({
        color: state.settings.color
    })
)(Currency);
