import {createAction, handleActions} from 'redux-actions';
import Actions from '../constants/actions'
import produce from 'immer';

const addCurrencies = createAction(Actions.ADD_MULTI_CURRENCIES);
const removeCurrency = createAction(Actions.REMOVE_MULTI_CURRENCY);
const reorderCurrency = createAction(
    Actions.REORDER_MULTI_CURRENCY,
    (prevIndex, newIndex) => ({prevIndex, newIndex})
);

const reducer = handleActions(
    {
        [Actions.ADD_MULTI_CURRENCIES] : (state, {payload: isos}) => produce(state, s => {
          isos.forEach(iso => {
            if (!s.includes(iso)){
              s.push(iso)
            }
          })
        }),
        [Actions.REMOVE_MULTI_CURRENCY] : (state, {payload: iso}) => produce(state, s => {
            const index = s.indexOf(iso);
            if (index >= 0) {
                s.splice(index, 1);
            }
        }),
        [Actions.REORDER_MULTI_CURRENCY] : (state, {payload: {prevIndex, newIndex}}) => produce(state, s => {
            const [removed] = s.splice(prevIndex, 1);
            s.splice(newIndex, 0, removed);
        })
    },
    ['USD', 'EUR']
);

const selector = (state) => (state.multi);

export {
  addCurrencies,
    removeCurrency,
    reorderCurrency,
    reducer,
    selector
}
