import {createAction, handleActions} from 'redux-actions';
import Actions from '../constants/actions'
import { MonetaryOptions } from '../constants'

const setFormats = createAction(Actions.SET_FORMATS, (monetary, precision) => {
  return {monetary, precision}
});

const reducer = handleActions(
  {
    [Actions.SET_FORMATS]: (state, {payload}) => payload,
  },
  {
    monetary: MonetaryOptions.COMMA_DOT,
    precision: '00'
  }
);

const selector = (state) => (state.formats);

export {
  setFormats,
  selector,
  reducer
}
