import React from 'react';
import styled from 'styled-components';
import { languages, lang2Flag } from "../../assets/codes";
import { isEmpty } from 'lodash';
import {Row, Col} from "../../components";
import Flag from "../../components/Flag";
import Space from "../../components/Space";

const allLanguages = (function(){
    const res = [];
    for (let key in languages) {
        if (languages.hasOwnProperty(key)){
            res.push({
                code: key,
                description: languages[key]
            })
        }
    }
    return res;
})();

const Component = ({keyword, style, className, onSelectItem}) => {
    const filteredList = React.useMemo(
        () => allLanguages.filter(item => {
            if (isEmpty(keyword)) return true;
            const l = keyword.toLowerCase();
            return item.code.includes(l) || item.description.toLowerCase().includes(l);
        }), [keyword]);


    return (
        <Container style={style} className={className}>
            {filteredList && filteredList.map(item => {
                return (
                    <ItemContainer onClick={() => onSelectItem && onSelectItem(item.code)} key={item.code}>
                        <Flag code={lang2Flag[item.code]} width={30}/>
                        <Space width={10}/>
                        <Text>{item.description}</Text>
                    </ItemContainer>
                )
            })}
        </Container>
    )
};

const Container = styled(Col)`
    width: 100%;
    overflow-y: scroll;
    max-height: 270px;
    background-color:white;
`;

const ItemContainer = styled(Row)`
    padding: 10px;
    border-bottom:1px solid rgb(200, 200, 200);
    align-items:center;
    justify-content: flex-start;
`;

const Text = styled.span`
    color: rgb(116, 116, 116);
    font-size: 16px;
    flex: 1;
`;

export default Component;
