/*global chrome*/
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import React from 'react'
import { Row } from './index'
import { webStoreRateLink } from '../constants'
import { useDispatch } from 'react-redux'
import { setRate } from '../redux/rates'
import {image} from "../utils";
import {useTranslation} from "react-i18next";

const Header = () => {
  const dispatch = useDispatch();
  const {t} = useTranslation();

  React.useEffect(() => {
    if (!chrome){
      return;
    }
    if (chrome.runtime.id) {
      chrome.runtime.sendMessage('fetch_rates', ({success, rates}) => {
        if (success) {
          // Update with new rates
          dispatch(setRate(rates))
        }
      });
      chrome.runtime.sendMessage('get_commands', (shortcut) => {
        window.shortcut_key = shortcut;
      });
    } else {
      dispatch(setRate({}));
      window.shortcut_key = 'ALT + C';
    }
  }, []);
  return (
    <View>
      <LogoView to={"/"}>
        <Image src={image('logo.svg')} alt="logo" data-tip={t('Currency converter')}/>
      </LogoView>
      <Flex/>
      <MenuView>
        <a href={webStoreRateLink} target={'_blank'}><img src={image('star1.png')} alt="star1"/></a>
        <ImageLink to="/multi"><LangImg src={image('multi.png')} alt="multi" data-tip={t('Multi-converter')}/></ImageLink>
        <ImageLink to="/settings"><LangImg src={image('decimal.png')} alt="decimal" data-tip={t('Currency format')}/></ImageLink>
        <ImageLink to="/language"><LangImg src={image('language-white-36dp.svg')} alt="language" data-tip={t('Language')}/></ImageLink>
        <ImageLink to="/prefs"><LangImg src={image('settings-wheel.svg')} alt={"settings"} data-tip={t('Settings')}/></ImageLink>
      </MenuView>
    </View>
  )
};

const View = styled(Row)`
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
`;

const LogoView = styled(Link)`
    padding-left: 5px;
`;

const Image = styled.img`
  width: 64px;
  height: 64px;
`;

const Flex = styled.div`
    flex: 1;
`;

const MenuView = styled.div`
    margin-top: -5px;
    margin-right: 15px;
`;

const ImageLink = styled(Link)`
    padding-left: 1em;
`;
const LangImg = styled.img`
  width: 34px;
`;
export default Header;
