import React from "react";
import styled from 'styled-components';
import RadioOptions from "../components/RadioOptions";
import {Col, ImageButton, Row, Screen} from '../components/index'
import {connect} from "react-redux";
import {setFormats,} from '../redux/formats'
import {useTranslation} from "react-i18next";
import {MonetaryOptions} from '../constants'
import {formatNumber, setupNumeral} from '../utils/format'
import numeral from "numeral";
import {image} from "../utils";

const monetaryOptions = [
    {
        value: MonetaryOptions.COMMA_DOT,
        title: '1,234.56'
    },
    {
        value: MonetaryOptions.DOT_COMMA,
        title: '1.234,56'
    },
    {
        value: MonetaryOptions.DOT,
        title: '1 234.56'
    },
    {
        value: MonetaryOptions.COMMA,
        title: '1 234,56'
    },
];

const decimalOptions = [
    {value: '00', title: '.02'},
    {value: '000', title: '.003'},
    {value: '0000', title: '.0004'},
    {value: '00000', title: '.00005'},
    {value: '000000', title: '.000006'},
    {value: '', title: 'Don\'t show', display: ''},
];

const findTitle = (options, value) => {
    const option = options.find(m => JSON.stringify(m.value) === JSON.stringify(value));
    return option?.display ?? option?.title ?? '';
};

const Settings = (props) => {
    const { t } = useTranslation();
    decimalOptions[5].title = t("Don't show");
    const preMonetaryTitle = 'Monetary format';
    const preDecimalTitle = 'Decimal format';
    const monetaryTitle = React.useMemo(
        () => {
            return t(preMonetaryTitle);
        }, [t]
    );
    const decimalTitle = React.useMemo(
        () => {
            return t(preDecimalTitle);
        }, [t]
    );

    const _changeSymbols = (option) => {
        props.setFormats(option, props.formats.precision);
        setupNumeral(option);
    };

    const _renderSampleFormat = () => {
        const p = props.formats.precision;
        return (
            <MonetaryText>
                {formatNumber(123456, '')}
                <DecimalText color={props.color}>
                    {p.length ? findTitle(decimalOptions, p).replace('.', numeral.localeData('en').delimiters.decimal) : ''}
                </DecimalText>
            </MonetaryText>
        )
    };

    return (
        <Screen>
            <View>
                <Row>
                    <ListView>
                        <ListText>{monetaryTitle}</ListText>
                        <form>
                            <RadioOptions
                                onChange={
                                    (value) => _changeSymbols(value)
                                }
                                options={monetaryOptions}
                                selected={props.formats.monetary}
                                name='monetary'
                                isMonetary
                            />
                        </form>
                    </ListView>
                    <ListView>
                        <ListText>{decimalTitle}</ListText>
                        <form>
                            <RadioOptions
                                onChange={
                                    (value) =>
                                    {
                                        props.setFormats(props.formats.monetary, value);
                                    }
                                }
                                options={decimalOptions}
                                selected={props.formats.precision}
                                name='decimal'/>
                        </form>
                    </ListView>
                </Row>
                <ListResult>
                    {_renderSampleFormat()}
                </ListResult>
            </View>
            <SaveView onClick={() => {
                props.history.goBack();
            }}>
                <ImageButton src={image('check_circle-white-36dp.svg')} alt="save"/>
            </SaveView>
        </Screen>
    )
};

const View = styled(Col)`
    background : white;
    padding : 1em;
`;
const ListView = styled(Col)`
    margin-left: 1em;
    flex: 1;
`;
const ListText = styled.p`
    padding-left: 1em;
    font-size: 15px;
`;
const ListResult = styled(Row)`
    justify-content: center;
    font-size: 25px;
`;
const MonetaryText = styled.p`
    font-weight: bold;
    margin-bottom: 0;
`;
const DecimalText = styled.span`
    color: ${props => props.color};
`;
const SaveView = styled(Row)`
    justify-content: center;
    margin : 1em;
`;
export default connect(
    state => ({
        formats: state.formats,
        color: state.settings.color,
    }),
    dispatch => ({
        setFormats: (monetary, precision) => dispatch(setFormats(monetary, precision))
    })
)(Settings);



