import numeral from 'numeral';
import { MonetaryOptions } from '../constants';

export function setupNumeral(monetary){
  let thousands = ',';
  let decimal = '.';

  if (monetary === MonetaryOptions.COMMA_DOT) {
    thousands = ',';
    decimal = '.';
  } else if (monetary === MonetaryOptions.DOT_COMMA){
    thousands = '.';
    decimal = ',';
  } else if (monetary === MonetaryOptions.DOT){
    thousands = ' ';
    decimal = '.';
  } else if (monetary === MonetaryOptions.COMMA) {
    thousands = ' ';
    decimal = ',';
  }

  numeral.localeData('en').delimiters = {
    thousands,
    decimal
  }
}

export function formatNumber(number, precision){
    // console.log('---------FormatNumber', number);
    if (Math.abs(number - 1) <= 0.0000001){
        return '1';
    }
    let format = '0,0';
    const _precision = precision || '';
    if (_precision.length) {
      format = format + '.' + _precision
    }
    return numeral(number).format(format);
}
