import React, {useEffect} from 'react';
import {connect} from "react-redux";
import styled from 'styled-components';
import {Col} from "../../components";
import Space from "../../components/Space";
import {webStoreRateLink} from "../../constants";
import {setAdDisplayed} from "../../redux/ad";
import {useTranslation} from 'react-i18next';

const question = 'Would you mind rating me in the Chrome Web Store?';
const buttonTitle = 'Rate now';
const stars = '    ';
const Component = ({adDisplayed, setAdDisplayed, color}) => {
    const [display, setDisplay] = React.useState(adDisplayed);
    const {t} = useTranslation();

    const rateTitle = t('Rate Title');
    const question = t('Rate description');
    const buttonTitle = t('Rate button1');
    const noThanks = t('Rate button2');

    useEffect(() => {
        setAdDisplayed();
    }, []);


    if (display){
        return (null);
    }

    return(
        <Container>
            <AdContainer>
                <Space height={10}/>
                <Stars>{stars}</Stars>
                <Space height={20}/>
                <RateTitle color={color}>{rateTitle}</RateTitle>
                <Space height={30}/>
                <Question>{question}</Question>
                <Space height={30}/>
                <RateNowButton as='a' href={webStoreRateLink} target={'_blank'} color={color}>
                    <RateNowTitle>{buttonTitle}</RateNowTitle>
                </RateNowButton>
                <Space height={15}/>
                <NoThanksButton href={webStoreRateLink} onClick={(e) => {e.preventDefault(); setDisplay(true);}}>{noThanks}</NoThanksButton>
            </AdContainer>
        </Container>
    )
};

const Container = styled(Col)`
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    justify-content:center;
    padding: 50px;
`;

const AdContainer = styled(Col)`
    background-color: white;
    border-radius: 10px;
    width: 100%;
    align-items: center;
    padding: 30px;
`;

const Stars = styled.span`
    color: grey;
`;

const RateNowButton = styled(Col)`
    height: 40px;
    align-self: stretch;
    background-color: ${props => props.color};
    align-items: center;
    justify-content: center;
    text-decoration: none;
    border-radius: 5px;
`;

const RateNowTitle = styled.span`
    color: white;
    font-size: 20px;
    font-weight: bold;
`;

const RateTitle = styled.span`
    color: ${props => props.color};
    font-size: 30px;
    text-align: center;
`;

const Question = styled.span`
    color: grey;
    font-size: 20px;
    text-align:center;
`;

const NoThanksButton = styled.a`
    color: rgb(180, 180, 180);
    font-size: 15px;
    text-decoration: underline;
    font-weight: 500;
`;

export default
connect(
    state => ({
        adDisplayed: state.ad,
        color: state.settings.color,
    }),
    dispatch => ({
        setAdDisplayed: () => dispatch(setAdDisplayed())
    })
)(Component);
