import codesMap from './codes-map';
import languages from './lang';
import currency2Symbols from './currency-symbols';

import { keys, uniq } from 'lodash';

const countryCodes = [];
let currencyISOs = [];
const country2ISO = {};
const iso2Flag = {};
const lang2Flag = {};

codesMap.forEach(o => {
    const countryCode = keys(o)[0];
    countryCodes.push(countryCode.toLowerCase());
    const currencyIso = o[countryCode];
    currencyISOs.push(currencyIso);

    country2ISO[countryCode.toLowerCase()] = currencyIso;
    iso2Flag[currencyIso] = countryCode.toLowerCase();
});

currencyISOs = uniq(currencyISOs);

