import React from "react";
import styled from 'styled-components';

// Options : {value, title}, selected: value
const RadioOptions = ({options, selected, name, onChange, isMonetary}) => {
    return (
        <React.Fragment>
            {
                options.map(({value, title}) => {
                    return (
                        <RadioView className='radio' key={name + title + value}>
                            {/*<input type="radio" value={value} name={name} checked={selected === value} onChange={() => onChange(value)}/>*/}
                            <input type="radio" value={value} name={name}
                                   checked={(isMonetary ? JSON.stringify(selected) : selected) === (isMonetary ? JSON.stringify(value) : value)}
                                   onChange={() => onChange(value)}/>
                            <RadioText onClick={() => onChange(value)}>{title}</RadioText>
                        </RadioView>
                    )
                })
            }
        </React.Fragment>
    )
};

const RadioView = styled.div`
    padding-left: 1em;
    padding-top: 0.7em;
`;
const RadioText = styled.label`
    padding-left: 1em;
    font-size: 15px;
`;

export default RadioOptions;
