import React from 'react';
import {ConnectedRouter} from "connected-react-router";
import {history} from "./redux/createStore";
import Header from "./components/Header";
import {Route} from "react-router-dom";
import Multi from "./pages/Multi";
import Main from "./pages/Main";
import Settings from "./pages/Settings";
import Language from "./pages/Language";
import AddMulti from "./pages/AddMulti";
import Prefs from "./pages/Prefs";
import Footer from "./components/Footer";
import RateUsAd from "./pages/RateUsAd";
import styled from "styled-components";
import {Col} from "./components";
import {connect} from "react-redux";
import ReactTooltip from 'react-tooltip';

const Routes = (props) => {
    return (
        <Content color={props.color} isGradient={props.isGradient}>
            <React.Suspense fallback={<div></div>}>
                <ConnectedRouter history={history}>
                    <Header/>
                    <Route exact path="/multi" component={Multi}/>
                    <Route exact path="/" component={Main}/>
                    <Route exact path="/settings" component={Settings}/>
                    <Route exact path="/language" component={Language}/>
                    <Route exact path="/multi/add" component={AddMulti}/>
                    <Route exact path="/prefs" component={Prefs} />
                    <Footer/>
                </ConnectedRouter>
                <RateUsAd/>
            </React.Suspense>
            <ReactTooltip place={'bottom'} effect={'solid'} backgroundColor={'white'} textColor={props.color} borderColor={props.color} border={true}/>
        </Content>
    );
};

const backgroundGradient = (props) => {
    if (!props.isGradient) {
        return '';
    }
    const color = props.color;
    if (color.length < 7) {
        return '';
    }
    const r = parseInt(color.substr(1, 2), 16);
    const g = parseInt(color.substr(3, 2), 16);
    const b = parseInt(color.substr(5, 2), 16);

    const bkImage = `background-image: linear-gradient(to right, rgb(${r}, ${g}, ${b}), rgb(${Math.min(255, r + 40)}, ${Math.min(255, g + 40)}, ${Math.min(255, b + 40)}))`;

    // console.log(bkImage);
    return bkImage;
};

const Content = styled(Col)`
  padding: 10px;
  flex: 1;
  position:relative;
  background-color: ${props => props.color};
  ${backgroundGradient};
`;

export default connect(
    state => ({
        isGradient: state.settings.gradient,
        color: state.settings.color
    })
)(Routes);
