import React, {useImperativeHandle} from 'react';
import styled from 'styled-components';
import {Row} from "../../components";
import {lang2Flag, languages} from '../../assets/codes';
import {image} from "../../utils";
import DownArrow from "../../components/DownArrow";
import Flag from "../../components/Flag";
import {useDelay} from "../../utils/hooks";
import {useTranslation} from "react-i18next";

const Component = ({style, className, onChangeSearch, onStopSearch, code, onStartSearch}, ref) => {
    const [isSearching, setSearching] = React.useState(false);
    const [searchValue, setSearchValue] = React.useState('');
    const { t } = useTranslation();

    const _startSearch = () => {
        setSearching(true);
        onStartSearch && onStartSearch();
    };

    const _stopSearch = useDelay(() => {
        setSearching(false);
        onStopSearch && onStopSearch();
    }, 200);

    const _onChangeSearch = (value) => {
        setSearchValue(value);
        onChangeSearch && onChangeSearch(value);
    };

    useImperativeHandle(ref, () => ({
        stopSearch: () => {
            _stopSearch();
        },
        searchValue: () => {
            return searchValue;
        }
    }));

    if (isSearching) {
        return (
            <Container style={style} className={className}>
                <SearchIcon src={image( 'find.png')} alt={'search'}/>
                <SearchInput
                    autoFocus
                    onBlur={_stopSearch}
                    onChange={(evt) => _onChangeSearch(evt.target.value)}
                    value={searchValue}
                    placeholder={t('Search')}
                />
                <DownArrow/>
            </Container>
        )
    } else {
        return(
            <Container style={style} className={className} onClick={_startSearch}>
                <Flag code={lang2Flag[code]}/>
                <CurrencyText>{languages[code]}</CurrencyText>
                <DownArrow/>
            </Container>
        )
    }
};

const Container = styled(Row)`
    background-color:white;
    height: 58px;
    padding: 10px;
    justify-content: flex-start;
    align-items: center;
`;

const SearchIcon = styled.img`
    width: 24px;
    height: 24px;
`;

const SearchInput = styled.input`
    border: none;
    flex: 1;
    width: 0;
    font-size: 18px;
    font-weight: bold;
    color: rgb(116, 116, 116);
    margin-left: 10px;
    margin-right: 10px;
`;
const CurrencyText = styled.span`
    margin-left:15px;
    flex:1;
    font-size: 18px;
    font-weight: bold;
    color: rgb(116, 116, 116);
    margin-left: 10px;
    margin-right: 10px;
`;

export default React.forwardRef(Component);
