import React from 'react'
import styled from 'styled-components'
import { Row } from '../../components'
import Currency from '../../components/Currency'
import { image } from '../../utils'
import Space from '../../components/Space'
import connect from 'react-redux/es/connect/connect'
import { convert } from '../../utils/rates'
import { setFavsValue } from '../../redux/favs'
import { formatNumber } from '../../utils/format'
import numeral from 'numeral';

const Item = ({iso, baseMoney, onRemove, from, value, rates, precision, monetary, ...props}) => {
  const [isEditing, setEditing] = React.useState(false);
  const [localValue, setLocalValue] = React.useState('');

  const _stopEditing = () => {
    setEditing(false);
  };

  const _startEditing = () => {
      setLocalValue(numeral(formatNumber(convert(rates, from, iso, value), props.formats.precision)).value());
    setEditing(true);
  }

  const _onChangeValue = (text) => {
    setLocalValue(text);
    const m = parseFloat(text);
    if (isNaN(m)){
      return;
    }

    const converted = convert(rates, iso, from, m);
    props.setFavsValue(converted);
  }


  const _renderContent = () => {
    if (isEditing) {
      return (
        <CurrencyInput
          autoFocus
          type='number'
          onBlur={_stopEditing}
          style={{color:'black'}}
          onChange={(evt) => _onChangeValue(evt.target.value)}
          onKeyDown={(evt) => {
            if (evt.keyCode === 27 || evt.keyCode === 13) {
              _stopEditing()
            }
          }}
          value={localValue}
        />
      )
    } else {
      const preValue = convert(rates, from, iso, value)
      const convertedValue = formatNumber(preValue, props.formats.precision)
      return (
        <Price onClick={_startEditing} color={props.color}>
          {value > 0 && convertedValue}
        </Price>
      )
    }
  };

  return (
    <Container>
      <Currency iso={iso}/>
      {_renderContent()}
      <Space width={10}/>
      <img src={image('up_down.png')} alt={'up_down'}/>
      <ClearButton onClick={onRemove}>
          <ClearButtonImage color={props.color}>
              <img src={image('clear-white-18dp.svg')} alt={'x'}/>
          </ClearButtonImage>
      </ClearButton>
    </Container>
  );
};


const CurrencyInput = styled.input`
  flex: 1;
  text-align: center;
  border: none;
  font-size: 20px;
  height: 100%;
  color: ${props => props.color};
`;

const Container = styled(Row)`
    margin-top: 5px;
    padding:10px;
    background-color: white;
    justify-content: center;
    align-items: center;
`;

const Price = styled.span`
    flex:1;
    color: ${props => props.color};
    font-size: 20px;
    text-align:center;
`;

const ClearButton = styled.div`
    padding-left: 10px;
    padding-right: 10px;
`;

const ClearButtonImage = styled.div`
    background-color: ${props => props.color};
    width: 20px;
    height: 20px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
`;

export default connect(
  state => ({
    from: state.favs.from,
    value: state.favs.value,
    rates: state.rates,
    formats: state.formats,
      color: state.settings.color,
  }),
  dispatch => ({
    setFavsValue: (value) => dispatch(setFavsValue(value))
  })
)(Item);
