import React from "react";
import styled from 'styled-components';
import {useTranslation} from "react-i18next";
import connect from "react-redux/es/connect/connect";
import { Row } from'../components'
import { Fill } from './'
import moment from 'moment'
import {facebookLink, twitterLink, websiteLink} from "../constants";
import {image} from "../utils";

const Footer = (props) => {
    const { t } = useTranslation();
    const preTitle = 'Powered by';
    const footerTitle = React.useMemo(
        () => {
            return t(preTitle);
        }, [t]
    );

    return (
        <FooterView>
            <FooterText>
                {footerTitle} <WikiLink href={websiteLink} target={'_blank'}>Currency.Wiki</WikiLink>
            </FooterText>
            {/* eslint-disable-next-line react/jsx-no-target-blank */}
            <a href={twitterLink} target={"_blank"}><FooterImage src={image('twitter.svg')} alt="twitter"/></a>
            {/* eslint-disable-next-line react/jsx-no-target-blank */}
            <a href={facebookLink} target={"_blank"}><FooterImage src={image('facebook.svg')} alt="facebook"/></a>
            <Fill/>
            <Today>
              {moment().format("MM/DD/YYYY")}
            </Today>
        </FooterView>
    )
};

const WikiLink = styled.a`
    text-decoration: none;
    color: white;
`;

const FooterView = styled(Row)`
    align-items:center;
    height: 25px;
`;
const FooterText = styled.span`
    color: white;
    font-size: 12px;
    font-weight: bold;
`;
const FooterImage = styled.img`
    padding-left: 10px;
    position: relative;
    width: 18px;
    height: 18px;
`;

const Today = styled.span`
    color: white;
    font-size: 11px;
    font-weight: bold;
`
export default connect(
    state => ({
        lang: state.lang
    })
)(Footer);
