import {createAction, handleAction} from 'redux-actions';
import Actions from '../constants/actions';

const setAdDisplayed = createAction(Actions.SET_AD_DISPLAYED);

const reducer = handleAction(
    Actions.SET_AD_DISPLAYED,
    (state, {payload}) => true,
    false
);

const selector = (state) => (state.ad);

export {
    setAdDisplayed,
    selector,
    reducer
}
