import {createAction, handleAction} from 'redux-actions';
import Actions from '../constants/actions';

const setRate = createAction(Actions.SET_RATES);
const reducer = handleAction(
    Actions.SET_RATES,
    (state, {payload: rate}) => {
        if (state.timestamp !== rate.timestamp){
            return rate;
        }
        return state;
    },
    {},
);
const selector = (state) => (state.rates);

export {
    setRate,
    selector,
    reducer
}
