import React from "react";
import LanguageSelector from "./LanguageSelector";
import {useTranslation} from "react-i18next";
import {selector, setLang} from "../../redux/lang";
import {connect} from "react-redux";
import LanguageList from "./LanguageList";
import { Screen } from '../../components'

const Language = ({lang, setLanguage}) => {
    const { i18n } = useTranslation();
    const [keyword, setKeyword] = React.useState('');
    const [isListVisible, setListVisible] = React.useState(false);
    return (
        <Screen>
            <LanguageSelector
                onStartSearch={() => setListVisible(true)}
                onStopSearch={() => setListVisible(false)}
                onChangeSearch={setKeyword}
                code={lang}
            />
            <LanguageList
                style={{marginTop: -1, display: isListVisible ? 'flex' : 'none'}}
                keyword={keyword}
                onSelectItem={(lang) => {
                    i18n.changeLanguage(lang);
                    setLanguage(lang)
                }}
            />
        </Screen>
    )
};

export default connect(
    state => ({
        lang: selector(state)
    }),
    dispatch => ({
        setLanguage: (lang) => dispatch(setLang(lang))
    })
)(Language);
