import React from 'react';
import styled from 'styled-components';
import {currencyISOs, iso2Flag, currency2Symbols} from "../../assets/codes";
import {connect} from "react-redux";
import {useTranslation} from "react-i18next";
import {Col, Row} from "../../components";
import Flag from "../../components/Flag";
import Space from "../../components/Space";
import {isEmpty} from 'lodash';

const Component = ({keyword, lang, style, className, onSelectItem}) => {
    const { t } = useTranslation();
    const [allList, setAllList] = React.useState([]);

    React.useEffect(() => {
        setTimeout(() => {
            setAllList(currencyISOs.map(iso => {
                return {
                    flag: iso2Flag[iso],
                    iso,
                    description: t(iso)
                }
            }))
        }, 200);
    },[]);

    const filteredList = allList.filter(item => {
        if (isEmpty(keyword)) return true;
        const l = keyword.toLowerCase();
        return item.flag.includes(l) || item.iso.toLowerCase().includes(l) || item.description.toLowerCase().includes(l);
    });
/*

    const allList = React.useMemo(
        () => {
            return currencyISOs.map(iso => {
                return {
                    flag: iso2Flag[iso],
                    iso,
                    description: t(iso)
                }
            });
        }, [lang]
    );
*/

/*
    const filteredList = React.useMemo(
        () => allList.filter(item => {
            if (isEmpty(keyword)) return true;
            const l = keyword.toLowerCase();
            return item.flag.includes(l) || item.iso.toLowerCase().includes(l) || item.description.toLowerCase().includes(l);
        }), [allList, keyword]);
*/
    return (
        <Container style={style} className={className}>
            {filteredList && filteredList.map(item => {
                return (
                    <ItemContainer onClick={() => onSelectItem(item.iso)} key={item.iso}>
                        <Flag code={item.flag} width={30}/>
                        <Space width={10}/>
                        <Text>{item.description} - {item.iso}({currency2Symbols[item.iso] || '$'})</Text>
                    </ItemContainer>
                )
            })}
        </Container>
    )
};

const Container = styled(Col)`
    position: absolute;
    max-height: 230px;
    left:0;
    top: 0;
    width:100%;
    overflow-y: scroll;
    background-color:white;
`;

const ItemContainer = styled(Row)`
    padding: 8px;
    border-bottom:1px solid rgb(200, 200, 200);
    align-items:center;
    justify-content: flex-start;
`;

const Text = styled.span`
    color: rgb(116, 116, 116);
    font-size: 16px;
    flex: 1;
`;


export default connect(
    (state) => ({
        // lang: selector(state)
        lang: state.lang
    })
)(Component);
