/*global chrome*/
import React from "react";
import styled from 'styled-components';
import {Col, ImageButton, Row, Screen} from '../../components/index'
import {connect} from "react-redux";
import {setGradient, setColor, setDefaultView} from '../../redux/settings'
import {useTranslation} from "react-i18next";
import {Fill} from "../../components";
import Space from "../../components/Space";
import ColorPicker from "./ColorPicker";
import {image} from "../../utils";

const Prefs = (props) => {
    const { t } = useTranslation();
    const handleGradientOptionChange = (evt) => {
        props.setGradient(evt.target.value == 0? true: false);
    };
    const handleDefaultViewOptionChange = (evt) => {
        props.setDefaultView(evt.target.value == 0 ? 0 : 1);
    };
    return (
        <Screen>
            <View>
                <Row>
                    <Text>{t('Color')}</Text>
                    <Fill/>
                    <Text>{t('Gradient Color')}</Text>
                    <Space width={20} />

                    <span style={{cursor:'pointer'}}>
                        <Text onClick={() => props.setGradient(true)}>{t('Turn on')}</Text>
                        <input type={'radio'} name={'gradient'} checked={props.isGradient} value={0} onChange={handleGradientOptionChange} style={{cursor:'pointer'}}/>
                    </span>

                    <Space width={10} />

                    <span style={{cursor:'pointer'}}>
                        <Text onClick={() => props.setGradient(false)}>{t('Turn off')}</Text>
                        <input type={'radio'} name={'gradient'} checked={!props.isGradient} value={1} onChange={handleGradientOptionChange} style={{cursor:'pointer'}}/>
                    </span>
                </Row>
                <StyledColorPicker onPickColor={props.setColor}/>
                <Space height={30}/>
                <Row>
                    <Text>{t('Default view')}</Text>
                </Row>
                <Space height={15}/>
                <Col style={{alignSelf: 'center', alignItems:'flex-end'}}>
                    <Row style={{cursor: 'pointer'}}>
                        <Text onClick={() => props.setDefaultView(0)}>{t('Currency converter')}</Text>
                        <input type={'radio'} name={'defaultView'} checked={props.defaultView == 0} value={0} onChange={handleDefaultViewOptionChange}/>
                    </Row>

                    <Space height={10}/>
                    <Row style={{cursor: 'pointer'}}>
                        <Text onClick={() => props.setDefaultView(1)}>{t('Multi-converter')}</Text>
                        <input type={'radio'} name={'defaultView'} checked={props.defaultView == 1} value={1} onChange={handleDefaultViewOptionChange}/>
                    </Row>
                </Col>
                <Space height={30}/>
                <Row>
                    <Text>{t('Shortcut')}</Text>
                </Row>
                <Space height={10}/>
                <Row style={{alignSelf: 'center', alignItems: 'center', }}>
                    <Text style={{fontSize:18}}>{window.shortcut_key}</Text>
                    <Space width={10} />
                    <ImageButton src={image('edit-24px.svg')} style={{width: 18, height: 18, marginTop: 2}} onClick={() => {
                        try {
                            const properties = {
                                "url": "chrome://extensions/shortcuts",
                                "active": true
                            };
                            chrome.tabs.update(null, properties);
                        }catch(ex){}
                    }}/>
                </Row>
            </View>
        </Screen>
    )
};

const View = styled(Col)`
    background : white;
    padding : 1em;
`;

const StyledColorPicker = styled(ColorPicker)`
    align-self: center;
    margin-top: 20px;
    width: 250px;
`;

const Text = styled.span`
    font-size: 15px;
`;

export default connect(
    state => ({
        isGradient: state.settings.gradient,
        color: state.settings.color,
        defaultView: state.settings.defaultView,
    }),
    dispatch => ({
        setGradient: (isGradient) => dispatch(setGradient(isGradient)),
        setColor: (color) => dispatch(setColor(color)),
        setDefaultView: (defaultView) => dispatch(setDefaultView(defaultView)),
    })
)(Prefs);



