import React from 'react';
import SearchBox from "./SearchBox";
import Space from "../../components/Space";
import CurrencyList from "./CurrencyList";
import produce from 'immer';
import styled from "styled-components";
import {ImageButton, Row, Screen} from '../../components'
import {addCurrencies} from '../../redux/multi'
import connect from "react-redux/es/connect/connect";
import {image} from "../../utils";

const Component = (props) => {
    const [keyword, setKeyword] = React.useState('');
    const [items, setItems] = React.useState([]);

    const _onSelectItem = (iso) => {
      setItems(produce(items, items => {
        const index = items.indexOf(iso);
        if (index >= 0){
          items.splice(index, 1)
        } else {
          items.push(iso)
        }
      }));
    }

    const _onSave = () => {
      props.addCurrencies(items)
      // Go back
      props.history.push('/multi');
    }

    // console.log(items);
    return (
        <Screen>
            <SearchBox onChangeSearch={setKeyword} color={props.color}/>
            <Space height={10}/>
            <CurrencyList onSelectItem={_onSelectItem} keyword={keyword} addedItems={props.addedItems} selectedItems={items}/>
            <SaveView onClick={_onSave}>
                <ImageButton src={image('check_circle-white-36dp.svg')} alt="save"/>
            </SaveView>
        </Screen>
    )
};

const SaveView = styled(Row)`
    justify-content: center;
    margin : 1em;
`;

export default connect(
    state => ({
        addedItems: state.multi,
        color: state.settings.color,
    }),
  dispatch => ({
    addCurrencies: (items) => dispatch(addCurrencies(items))
  })
)(Component);
